body {
  margin: 0 auto;
}

.stars--0,
.stars--1,
.stars--2,
.stars--3,
.stars--4,
.stars--5 {
  display: flex;
}

.stars__star {
  background: url(images/star.svg) center center no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 *:first-child,
.stars--2 *:nth-child(-n + 2),
.stars--3 *:nth-child(-n + 3),
.stars--4 *:nth-child(-n + 4),
.stars--5 *:nth-child(-n + 5) {
  background: url(images/star-active.svg) center center no-repeat;
}
